@import url('https://fonts.googleapis.com/css?family=Poppins:200,300,400,500,600,700,800,900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}


section {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    overflow-x: hidden; 
    background-image: linear-gradient(-135deg,#c850c0,#4158d0);
}

/* fix this*/
.logo{
    justify-content: space-between;
}
.logo img {
    width: 10%;
    height: 12%;
    color: #383838;
    margin-bottom: 10px;
}

.login {
    position: relative;
    padding: 60px;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(15px);
    border: 1px solid rgba(255, 255, 255, 0.15); /* check #fff */
    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
    border-right: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 20px;
    width: 500px;
    display: flex;
    flex-direction: column;
    gap: 30px;

}

.login form {
    position: relative;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.login .logo {
    justify-content: space-between;
    position: relative;
    width: 100%;
    font-size: 1.6em;
    font-weight: 800;
    color: #383838;
    margin-bottom: 10px;
    display: block;
}

.login .title {
    position: relative;
    width: 100%;
    text-align: center;
    font-size: 2.5em;
    font-weight: 600;
    color: #f5f5f5;
    margin-bottom: 10px;
}

.login form .inputBox {
    position: relative;
}

.login form .inputBox input {
    position: relative;
    width: 100%;
    padding: 15px 20px;
    outline: none;
    font-size: 1.25em;
    color: #707070;
    border-radius: 5px;
    background: #fff;
    border: none;
    margin-bottom: 30px;

}



.login form .inputBox input::placeholder {
    color: #707070;
}

.login form .inputBox .error {
    border-color: #f03939;
    border-style: solid;
    border-width: 3.1px;
}

.login form .inputBox .error-msg {
    position: absolute;
    top: 67px;
    left: 10px;
    font-size: 0.9em;
    font-weight: 560;
    color: #b02c2c;
}

.login form .inputBox .error-msg a{
    cursor: pointer;
    text-decoration: underline;
}

.login form .inputBox #btn {
    position: relative;
    border: none;
    outline: none;
    background: linear-gradient(-135deg,#c850c0,#4158d0);
    color: #fff;
    cursor: pointer;
    font-size: 1.25em;
    font-weight: 500;
    transition: 0.5s;
}

.login form .inputBox #btn:hover {
    background: linear-gradient(-135deg,#ec17dd, #4662f2);
    transition: all ease 1.5s;
}

.login form .group {
    display: flex;
    justify-content: space-between;
}

.login form .group a {
    font-size: 1.25em;
    color: #fff;
    font-weight: 500;
    text-decoration: none;
    transition: 0.5s;
    text-decoration: underline;
    cursor: pointer;
}

.login form .group a:hover {
    color: #3c33ee;
    transition: 0.5s;
}

.material-symbols-outlined {
    font-variation-settings:
    'FILL' 1,
    'wght' 500,
    'GRAD' 200,
    'opsz' 40
}





