@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700;800;900&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Poppins', sans-serif;
}

.body {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    height: auto;
    background: linear-gradient(-135deg,#c850c0,#4158d0);
    background:-webkit-linear-gradient(-135deg,#c850c0,#4158d0);
    min-width: 848px;
}


section {
    position: absolute;
    display: flex; 
    align-items: center; 
    justify-content: center; 
    min-height: 100vh;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    min-width: 848px;
}

    
.app-background {
    position: absolute;
    width: 90%;
    min-width: 848px;
    height: 100%;
    min-height: 400px;
    max-height: 550px;
    background-color: rgba(255,255,255,.4);
    border-radius: 30px;
    border: 1px solid rgba(255, 255, 255, .25);
    border-top: 1px solid rgba(255,255,255,.5);
    border-left: 1px solid  rgba(255,255,255,.5);
    box-shadow: 10px 20px 30px rgba(0,0,0,.1);
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 50px 0 70px 0;
}

.main-container {
    position: absolute;
    width: 95%;
    /* max-width: 1050px; changed */
    /* height: calc(100vh - 40px); */
    height: 92%;
    /* margin-left: 15px; */
    /* margin-top: 20px; */
    display: flex;
    box-shadow: 0px 1px 1px  0 rgba(0,0,0,0.5), 0px 2px 5px 0 rgba(0,0,0,0.6);
}

.main-container .left-container {
    position: relative;
    width: 100%;
    height:100%;
    background: #fff;
}

.main-container .right-container {
    position: relative;
    width: 100%;
    height: 100%;

}

.main-container .right-container::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
}

header {
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 60px;
    background: #ededed;
    padding: 0 15px;
}

.user-img {
    position:relative;
    width: 40px;
    height: 40px;
    overflow: hidden;
    border-radius: 50%;
}

.dp {
    position:absolute;
    top: 0px;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    cursor: pointer;
}

.nav-icons {
    display:flex;
    justify-content: flex-end;
    margin-left: auto; /* changed */
}

.nav-icons li {
    list-style: none;
    display: flex;
    cursor: pointer;
    color: #295772;
    margin-left: 22px;
    font-size: 18px;
}

.notif-box {
    position: relative;
    display: flex;
    width: 100%;
    height: 70px;
    background: #76daff;
    align-items: center;
    font-size: 0.8em;
    text-decoration: none;
}

.notif-box i {
    position:relative;
    left: 13px;
    background:#fff;
    padding:10px;
    width: 42px;
    height: auto;
    font-size: 20px;
    border-radius: 55%;
    cursor: pointer;
    color:#76daff;
}

.notif-box .fa-xmark {
    position: absolute;
    left: 260px;
    text-align:center;
    background:#76daff;
    color: #fff;
}

.notif-text {
    margin: 25px;
}

.notif-text a {
    text-decoration: none;
    color: #333;
    font-size: 0.9em;
}
.search-container {
    position: relative;
    width: 100%;
    height: 40px;
    background: #f6f6f6;
    display: flex;
    align-items: center;
  }
  
  .search-container .input {
    position: relative;
    flex-grow: 1; /* Allow the input to grow and occupy remaining space */
  }
  
  .search-container .input input {
    width: 97.5%;
    outline: none;
    border: none;
    background: #fff;
    padding: 5px;
    height: 30px;
    border-radius: 10px;
    font-size: 12px;
    padding-left: 35px;
    margin: 6px;
  }
  
  .search-container .input i {
    position: absolute;
    left: 20px;
    top: 15px;
    color: #bbb;
    font-size: 0.8em;
  }
  
.chat-list {
    position: relative;
    height:calc(100% - 123px);
    overflow-y: auto;
}

.chat-list .chat-box {
    position: relative;
    width: 100%;
    display:flex;
/*   justify-content: center; */
    align-items:center;
    cursor: pointer;
    padding: 15px;
    border-bottom: 1px solid #eee;
}

.chat-list .chat-box .img-box {
    position:relative;
    width: 55px;
    height:45px;
    overflow:hidden;
    border-radius: 50%;
}

.chat-list .chat-box .chat-details {
    width: 100%;
    margin-left: 10px;
}

.chat-list .chat-box .chat-details .text-head {
    display:flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom:2px;
}

.chat-list .chat-box .chat-details .text-head h4 {
    font-size: 1.1em;
    font-weight: 600;
    color: #000;
}

.chat-list .chat-box .chat-details .text-head .time {
    font-size: 0.8em;
    color: #aaa;
}

.chat-list .chat-box .chat-details .text-message {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chat-list .chat-box .chat-details .text-message p {
    color: #aaa;
    font-size: 0.9em;
    overlay: hidden;
}

img {
    width: 100%;
    object-fit: cover;
}

.chat-list .chat-box .chat-details .text-message b {
    background: #06e744;
    color: #fff;
    min-width: 20px;
    height: 20px;
    border-radius: 50%;
/*   text-align: center; */
    font-size: 0.8em;
    font-weight: 400;
    display:flex;
    justify-content:center;
    align-items:center;
}

.chat-list .active {
    background: #ebebeb;
}

.chat-list .chat-box:hover {
    background: #f5f5f5;
}

.chat-list .chat-box .chat-details .text-head .unread {
    color: #06e744;
}


/* right-container */


.right-container header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.right-container header .img-text .user-img .dp {
    position:relative;
    top: -2px;
    left: 0px;
    width: 40px;
    height:auto;
    overflow:hidden;
    object-fit: cover;
}

.right-container header .img-text {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.right-container header .img-text h4 {
    font-weight: 450;
    line-height: 0.8em;
    margin-left: 15px;
    margin-top: 15px;
}

.right-container header .img-text h4 span {
    font-size: 0.5em;
    color: #555;
}

.right-container header .nav-icons {
    position: relative;
    margin-right:0px;
/*   padding: 5px; */
}

.right-container header .nav-icons i {
    padding: 10px;
}

.chat-container {
    position:relative;
    width: 100%;
    height: calc(100% - 120px);  /*60+60*/
    padding: 50px;
    overflow-y: auto;
}

.message-box {
    position:relative;
    display: flex;
    width:100%;
    margin: 5px 0;
}

.message-box p {
    position:relative;
    right: 0;
    text-align: right;
    max-width: 65%;
    padding: 12px;
    background: #dcf8c6;
    border-radius: 10px;
    font-size: 0.9em;
}

.message-box.my-message p::before {
    content : '';
    position: absolute;
    top: 0;
    right: -12px;
    width: 20px;
    height: 20px;
    background: linear-gradient(135deg, #dcf8c6 0%, #dcf8c6 50%, transparent 50%, transparent);
}

.message-box p span {
    display: block;
    margin-top: 5px;
    font-size: 0.8em;
    opacity: 0.5;
}

.my-message {
    justify-content: flex-end;
}

.friend-message p {
    background: #fff;
}

.friend-message {
    justify-content: flex-start;
}

.chat-container .my-message i {
    color: yellow;
}

.message-box.friend-message::before {
    content : '';
    position: absolute;
    top: 0;
    left: -12px;
    width: 20px;
    height: 20px;
    background: linear-gradient(225deg, #fff 0%, #fff 50%, transparent 50%, transparent);
}

.chatbox-input {
    position:relative;
    width: 100%;
    height: 60px;
    background: #f0f0f0;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.chatbox-input i {
    cursor: pointer;
    font-size: 1.8em;
    color: #515855;
}

.chatbox-input i:nth-child(1) {
    margin: 15px;
}

.chatbox-input i:last-child {
    margin-right: 25px;
}

.chatbox-input input {
    position: relative;
    width: 90%;
    margin: 0 20px;
    padding: 10px 20px;
    border-radius:10px;
    font-size: 1em;
    border:none;
    outline:none;
}

.mt-left{
    display: flex;
    justify-content: space-between;
    flex: 1;
}

.material-symbols-outlined {
    font-variation-settings:
    'FILL' 1,
    'wght' 500,
    'GRAD' 200,
    'opsz' 40
}

.logo {
    width: 100%;
    font-size: 1.3em;
    font-weight: 900;
    color: #383838;
    display: flex;
    justify-content: right;
}

.logout {
    background-color: #a66aa2;
    padding: 8px 12px; /* Adjust the padding values as needed */
    font-size: 11px; /* Adjust the font size as needed */
    margin-top: 10px;
  }

.addUser {
    color: #4158d0;
    margin-top: 17px;
}

  
/*emoji's part*/

  
  .emoji-picker-container {
    position: absolute;
    top: -80px; /* Adjust this value to control the distance from the icon */
    right: 295px;
    width: 220px; /* Adjust this value to control the width of the emoji picker */
    background-color: #ffffff;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    z-index: 1;
    display: none;
  }
  
  .emoji-picker-container.show {
    display: block;
  }
  
  .emoji {
    font-size: 24px;
    cursor: pointer;
    padding: 4px;
  }
  
  .fa-regular.fa-face-grin {
    cursor: pointer;
    margin-right: 20px;
    margin-left: 20px;
  }
  
  .fa-regular.fa-face-grin:hover {
    color: #a66aa2; /* Adjust this value to change the hover color */
  }
 
  .user-data-display{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .user-data-display .user-img {
    margin-right: 10px; /* Adjust the margin as needed */
  }