.container .logo {
    width: 100%;
    font-size: 1.3em;
    font-weight: 900;
    color: #383838;
    display: flex;
    justify-content: left;
}

.container form .user-details{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 20px 0 12px 0;
}

.container form .user-details .input-box {
    margin-bottom: 15px;
    width: calc(100% / 2 - 20px);
}

.container form .user-details .input-box .details{
    display: block;
    font-weight: 500;
    margin-bottom: 5px;
}

.container form .user-details .input-box input{
    height: 45px;
    width: 100%;
    outline: none;
    border-radius: 5px;
    border: 1px solid #ccc;
    padding-left: 15px;
    font-size: 16px;
    border-bottom-width: 2px;
    transition: all 0.3 ease;
    background-color: #fff;
}

.container form .user-details .input-box input.error {
    border: 2px solid red;
    transition: all 0.3 ease;
}

.container form .user-details .input-box .error-msg {
    color: red;
    font-size: 13px;
    font-weight: 500;
    margin-top: 5px;
    margin-left: 5px;
}

.container form .user-details .input-box input.correct {
    border: 2px solid rgb(54, 255, 40);
    transition: all 0.3 ease;
}


form .submit-btn {
    position: relative;
    height: 45px;
    width: 25%;
    margin: 45px 40%;
}

form .submit-btn input{
    height: 100%;
    width: 100%;
    outline: none;  
    border: none; 
    color: #fff;
    background: linear-gradient(-135deg,#c850c0,#4158d0);
    font-size: 30px;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3 ease;   
    letter-spacing: 0.7px;
}

form .submit-btn input:hover{
    transform: scale(0.99);
    background: linear-gradient(-135deg,#ec17dd, #4662f2);
}

.user-details .link-to-sign-in {
    display: flex;
    align-items: center;
    justify-content: center;

    flex: 1;
}

.user-details .link-to-sign-in a {
    font-size: 1.25em;
    color: #fff;
    font-weight: 500;
    text-decoration: none;
    transition: 0.5s;
    text-decoration: underline;
    text-align: center;
    cursor: pointer;
}

.user-details .link-to-sign-in a:hover {
    color: #190dff;
    transition: 0.5s;
}

.material-symbols-outlined {
    font-variation-settings:
    'FILL' 1,
    'wght' 700,
    'GRAD' 200,
    'opsz' 40
}


.input-box .details i.material-symbols-outlined.question{
    position: relative;
    transform: translateY(20%);
    margin-left: 5px;
    color: #383838;
    font-variation-settings: 'FILL' 3, 'wght' 700, 'GRAD' 100, 'opsz' 48;
}

.profile-pic {
    position: relative;
    width: 60px;
    height: 60px;
    border-radius: 50%;
    margin-left: 35%;
    margin-top: 5%;
    margin-bottom: 0;
}




@media (max-width: 584px) {
    .container{
        max-width: 80%;
    }
    form .user-details .input-box {
        margin-bottom: 15px;
        width: 100%;
    }
    .container form .user-details{
        max-height: 300px;
        overflow-y: scroll;
    }
    .user-details::-webkit-scrollbar{
        width: 5px;
    }
}










